.home-main{
    width: 100%;
    height: 100%;
    max-height: 100%;
    overflow-y: unset;
    background: #6600DE8E;
    transition: background .8s;
}
.profile-div {
    height: 100%;
    border-right: 1px solid #bbb;
}
.space-div{
    width: 100%;
    height: 25%;
}
.static-text{
    width: 100%;
    height: 20%;
    font-size: 5vw;
    text-align: start;
    margin-left: 10%;
    color: white;
    font-family: 'Alata', sans-serif;
    font-weight: bold;
}
.space-div-bot{
    width: 100%;
    height: 25%;
}
@font-face {
    font-family: 'amestel';
    src: url('/font/aemstel-lineInside.otf');
}
.typed-text{
    width: 100%;
    height: 10%;
    text-align: start;
    color: white;
    font-size: 4vw;
    font-family: 'amestel', sans-serif;
    text-align: start;
    margin-left: 10%;
    text-transform: lowercase;
}
input:focus{
    outline: none;
}
.typed-style{
    background: #00000000;
    width: 100%;
    color: white;
    border: 0px;
}
.selection-area{
    margin-top: 5%;
    width: 100%;
    height: 20%;
    padding-left: 5%;
    text-align: center;
}
.home-btn{
    width : 18%;
    float : left;
    margin-right : 2%;
}
.creator-box{
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient(to left, #ff5d3c, #ff2f65);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #ff5d3c, #ff2f65); 
    border-radius: 8%;
    cursor: pointer;
    padding: 2%;
    display: inline-block;
}
.gamer-box{
    width: 100%;
    height: 100%;
    background: #76b900;
    border-radius: 8%;
    cursor: pointer;
    padding: 2%;
    display: inline-block;
}
.scientific-box{
    width: 100%;
    height: 100%;
    background: #161616;
    border-radius: 20px;
    padding: 2%;
    cursor: pointer;
    display: inline-block;
}
.button-main-text{
    width : 100%;
    color : white;
    padding-top : 15%;
    font-weight : bold;
    text-align : start;
    padding-left : 4%;
}
.button-detail{
    margin-top: 10%;
    padding-left : 10%;
    width : 100%;
    color : white;
    padding-top : 10%;
    font-size : 0.8vw;
    text-align : start;
}
.comming-soon{
    padding-left : 10%;
    width : 100%;
    color : white;
    padding-top : 2%;
    font-size : 0.8vw;
    text-align : start;
}
.build-pc-btn{
    border: 1px solid white;
    width : 200px;
    height: 70%;
    color :white;
}
.button-icon{
    width: 100%;
    height: 100%;
}
::placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
   color: white;
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
   color: white;
  }
  @media screen and (max-width: 767px){
	.creator-box{
        width: 100%;
        height: 80%;
        background: -webkit-linear-gradient(to left, #ff5d3c, #ff2f65);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to left, #ff5d3c, #ff2f65); 
        border-radius: 20px;
        cursor: pointer;
        padding: 2%;
        display: inline-block;
    }
    .button-icon{
        width: 20px;
        height: 20px;
        margin-left: 10%;
    }
    .gamer-box{
        width: 100%;
        height: 80%;
        background: #76b900;
        border-radius: 20px;
        cursor: pointer;
        padding: 2%;
        display: inline-block;
    }
    .scientific-box{
        width: 100%;
        height: 80%;
        background: #161616;
        border-radius: 20px;
        padding: 2%;
        cursor: pointer;
        display: inline-block;
    }
    .home-btn{
        width : 100%;
    }
    .button-main-text{
        width : 100%;
        color : white;
        padding-top : 5%;
        font-weight : bold;
        text-align : start;
        padding-left : 4%;
    }
    .button-detail{
        padding-left : 10%;
        width : 100%;
        color : white;
        padding-top : 0%;
        margin-top: 2%;
        font-size : 10px;
        text-align : start;
    }
    .in-mobile-disappear{
        display: none;
    }
    .static-text{
        width: 100%;
        height: 10%;
        margin-top: 5%;
        font-size: 20px;
        text-align: start;
        margin-left: 10%;
        color: white;
        font-family: 'Alata', sans-serif;
        font-weight: bold;
    }
}