.transaction-card{
    width: 100%;
    height: 100%;
    border: 1px solid white;
    border-radius: 10px;
}
.trans-card-header{
    height: 3vw;
    width: 100%;
    text-align: left;
    color: #01d8bc;
    padding: 1%;
    font-size: 1.2vw;
    border-bottom: 0.8px solid #fff;
}
.trans-data-div{
    padding: 1%;
    width: 100%;
    height: 25vw;
}
.data-col{
    border-right : 0.6px solid white;
}
.trans-col-hedaer{
    text-align: start;
    color: #01d8bc;
    font-size: 1.2vw;
    width: 100%;
    height: 2vw;
}
.trans-col-data{
    text-align: start;
    color: white;
    padding-left: 2%;
    font-size: 1vw;
    width: 100%;
    margin-bottom: 2%;
}