.ship-main{
    width: 100%;
    height: 100%;
    max-height: 100%;
    overflow-y: scroll;
    background: #000000;
    padding-top: 3%;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 2%;
    text-align: center;
}
.ship-header{
    width: 100%;
    text-align: start;
    padding-left: 20%;
    color: white;
    font-size: 1.4vw;
    font-weight: bold;
}
.ship-selection-div{
    padding-right: 25%;
    padding-left: 15%;
    margin-top: 5%;
    display: inline-block;
    height: 75%;
    width: 100%;
}
.ship-data-entry{
    width: 37%;
    height: 70%;
    float: left;
    margin-left: 25%;
}
.ship-image-div{
    width: 100%;
    height: 80%;
    float: left;
    margin-left: 10%;
}
.ship-form{
    width: 100%;
    height: 10%;
}
.text-field-data{
    width: 100%;
    border-bottom: 1px solid white;
    border-top: 0px solid white;
    border-left: 0px solid white;
    border-right: 0px solid white;
    background: #000000;
    color: white;
}
.text-input-field:fous{
    border-bottom: 1px solid white;
}
.text-block{
    width: 100%;
    height: 70px;
    margin-left: 10%;
}
.order-summary-box{
    padding: 10%;
    color: white;
    margin-left: 30%;
    width: 100%;
    height: 90%;
    border: 1px solid #ffffff;
}
.summary-header{
    font-size: 1.3vw;
    height: 10%;
}
.order-detail-div{
    height: 70%;
}
.order-total-div{
    height: 20%;
}
.order-data{
    margin-top: 2%;
    font-size: 1.1vw;
}
.divider-line{
    width: 100%;
    background: white;
    height: 2px;
}
.view-change-selected{
    color: white;
    font-weight: bold;
}
.view-change-unselected{
    color: white;
}
.switch-class{
    background: #37474F;
    border-radius: 10px;
    margin-left: 5%;
    margin-right: 5%;
}
.MuiSwitch-colorSecondary{
    color : white;
}
.col-name-div{
    width: 60%;
    float: left;
    height: 100%;
    color: white;
    font-size: 1.1vw;
}
.col-op-div{
    width: 40%;
    float: left;
    height: 100%;
    font-size: 1.1vw;
    color: white;
}
.radio-btn{
    cursor: pointer;
}
.address-block-height{
    height: 100%;
}
@media screen and (max-width: 767px){
    .ship-main{
        width: 100%;
        height: 100%;
        max-height: 100%;
        overflow-y: scroll;
        background: #000000;
        padding-top: 3%;
        padding-bottom: 2%;
        text-align: center;
    }
    .ship-header{
        width: 100%;
        text-align: start;
        padding-left: 20%;
        color: white;
        font-size: 10px;
        font-weight: bold;
    }
    .ship-selection-div{
        padding-right: 10%;
        padding-left: 0%;
        margin-top: 5%;
        display: inline-block;
        height: 75%;
        width: 100%;
    }
    .col-name-div{
        width: 60%;
        float: left;
        height: 100%;
        color: white;
        font-size: 15px;
    }
    .col-op-div{
        width: 40%;
        float: left;
        height: 100%;
        font-size: 15px;
        color: white;
    }
    .address-block-height{
        height: 130%;
    }
    .order-summary-box{
        padding: 10%;
        color: white;
        margin-left: 10%;
        width: 100%;
        height: 100%;
        border: 1px solid #ffffff;
    }
    .summary-header{
        font-size: 10px;
        height: 10%;
    }
    .order-detail-div{
        height: 75%;
    }
    .order-total-div{
        height: 20%;
    }
    .order-data{
        margin-top: 2%;
        font-size: 10px;
    }
    .checkout-btn-div{
        margin-top: 120%;
        width: 100%;
        height: 10%;
    }
    .ship-next-button-style{
        width: 85%;
        height : 100%;
        border-radius : 25px;
        background : #4CAF50;
        text-align : center;
        font-size : 20px;
        font-weight : bold;
        padding-top : 5%;
        margin-left: 10%;
        margin-right: 10%;
        color : white;
        cursor: "pointer";
    }
}