.software-lg-image-div{
    height: 60%;
    width: 40%;
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 2%;
    z-index: 1;
}
.software-lg-name{
    height: 20%;
    width: 100%;
    padding-left: 10%;
    text-align: start;
    font-size: 1vw;
    color: #00e3c5;
    font-weight: bold;
}
.software-lg-price{
    height: 20%;
    width: 100%;
    text-align: start;
    font-size: 1vw;
    margin-top: 2%;
    padding-left: 10%;
    color: white;
    font-weight: bold;
}
@media screen and (max-width: 767px){
    .software-lg-image-div{
        height: 40%;
        width: 40%;
        margin-left: 10%;
        margin-right: 10%;
        margin-bottom: 2%;
        z-index: 1;
    }
    .software-lg-name{
        width: 100%;
        padding-left: 10%;
        text-align: start;
        font-size: 10px;
        color: #00e3c5;
        font-weight: bold;
    }
    .software-lg-price{
        width: 100%;
        text-align: start;
        font-size: 10px;
        margin-top: 20px;
        padding-left: 10%;
        color: white;
        font-weight: bold;
    }
}