.accessories-main{
    width: 100%;
    height: 100%;
    background: #000000;
    padding-top: 2%;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 2%;
    text-align: center;
}
.greet-header{
    width: 100%;
    text-align: start;
    padding-left: 20%;
    color: white;
    font-size: 1.4vw;
}
.ssoftware-div-1{
    display: inline-block;
    height: 20%;
    width: 100%;
    background: #eee;
}
.software-display{
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 12%;
    padding-right: 12%;
    width: 100%;
    height: 60%;
}
.software-div{
    width: 15%;
    height: 45%;
    float: left;
    margin-right: 5%;
    margin-bottom: 2%;
}
.software-lg-div{
    width: 40%;
    height: 45%;
    float: left;
    margin-right: 5%;
    margin-bottom: 2%;
}
.pricing-div{
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 12%;
    padding-right: 12%;
    width: 100%;
    height: 35%;
}
.pricae-container-div{
    width: 40%;
    height: 45%;
    float: left;
    margin-right: 5%;
    margin-left: 10%;
    margin-bottom: 2%;
}
.item-data-div{
    padding-top: 15px   ;
    color: white;
    font-size: 1.3vw;
    width: 100%;
    height: 60%;
    max-height: 60%;
    overflow-y: scroll;
    border: 1px solid white;
}
.final-price-div{
    color: white;
    width: 100%;
    height: 40%;
    border: 1px solid white;
}
.full-width{
    text-align: start;
    width: 100%;
}
.shipping-detail-div{
    text-align: start;
    font-size: 0.8vw;
    color: white;
}
.final-price{
    text-align: start;
    font-size: 1.4vw;
}
.next-button-style{
    width: 100%;
    height : 70%;
    border-radius : 25px;
    background : #00e3c5;
    text-align : center;
    font-size : 1.7vw;
    margin-top :5%;
    margin-bottom :2%;
    font-weight : bold;
    padding-top : 2%;
    color : white;
    cursor: pointer;
}
@media screen and (max-width: 767px){
    .accessories-main{
        width: 100%;
        height: 100%;
        background: #000000;
    }
    .greet-header{
        width: 100%;
        text-align: start;
        padding-left: 10%;
        color: white;
        font-size: 10px;
    }
    .software-display{
        padding-top: 2%;
        padding-bottom: 2%;
        width: 100%;
        height: 60%;
    }
    .software-div{
        width: 25%;
        height: 30%;
        float: left;
        margin-right: 5%;
        margin-bottom: 2%;
    }
    .pricae-container-div{
        width: 100%;
        height: 30%;
        margin-right: 0%;
        margin-left: 5%;
        margin-bottom: 2%;
    }
    .item-data-div{
        padding-top: 15px   ;
        color: white;
        font-size: 10px;
        width: 100%;
        height: 70%;
        max-height: 70%;
        overflow-y: scroll;
        border: 1px solid white;
    }
    .final-price-div{
        color: white;
        width: 100%;
        height: 30%;
        border: 1px solid white;
    }
    .next-button-style{
        width: 100%;
        height : 50%;
        border-radius : 25px;
        background : #00e3c5;
        text-align : center;
        font-size : 15px;
        margin-top :5%;
        margin-bottom :2%;
        font-weight : bold;
        padding-top : 2%;
        color : white;
        cursor: pointer;
    }
    .final-price{
        display: none;
    }
    .shipping-detail-div{
        font-size: 8px;
        color: white;
    }
}