.final-build-main{
    width: 100%;
    height: 100%;
    max-height: 100%;
    overflow-y: scroll;
    background: #000000;
    padding-top: 2%;
}
.list-item{
    height: 10%;
    width: 100%;
}
.rectangle-button {
    margin-left: 5px;
    width: 30px;
    height: 20px;
    border-radius: 28.5px;
    border: solid 1px #979797;
    float: left;
    text-align: center;
    cursor : pointer;
    font-weight: bold;
}
.carot-icon{
    font-size: 10px;
    cursor: pointer;
}
.carot-icon-show-more{
    font-size: 10px;
    cursor: pointer;
    margin-left: -20%;
}
.carot-up-icon-div{
    margin-top: -11%;
    height: 30%;
    padding: 0;
    width: 100%;
    padding-bottom: 10%;
    padding-left: 70%;
}
.data-btw-carot{
    height: 40%;
    width: 100%;
    text-align: start;
}
.carot-down-icon-div{
    height: 30%;
    margin-top: -2%;
    width: 100%;
    padding-left: 70%;
}
.pc-image{
    margin-left: 30%;
    width: 40%;
    height: 40%;
    border-left: 1px solid white;
    padding-left: 5%;
}
.x-dim{
    width: 150px;
    margin-left: 40%;
    font-size: 1.4vw;
    border-top: 1px solid white;
    text-align: center;
    color: white;
}
.y-dim{
    float: left;
    margin-top: 20%;
    margin-left: 11%;
    font-size: 1.4vw;
    color: white; 
}
.z-dim{
    width: 150px;
    margin-left: 40%;
    font-size: 1.4vw;
    border-bottom: 1px solid white;
    text-align: center;
    color: white; 
    margin-top: 20px;
}
.config-icon{
    width: 100%;
    height: 8%; 
    padding-left: 20%;  
    padding-top:4%;
}
.icon-div{
    width: 50px;
    height: 40px;
    float: left;
    margin: 10px;
    cursor: pointer;
}
.icon-sub-div{
    width :100%;
}
.icon-config{
    margin-left: 30%;
    height: 20px;
    width: 20px;
}
.icon-name{
    width: 100%;
    margin-top: 10px;
    font-size: 10px;
    color: white;
    text-align: center;
    padding-left: -50px;
}
.extra-padding{
    padding: 1px;
}
.wattage-div{
    width: 100%;
    height: 50px;
    text-align: center;
}
.game-selected-show{
    margin-top: 10%;
    width: 100%;
    height: 30%;
}
.game-monitor-show{
    margin-top: 10%;
    width: 100%;
    text-align: start;
    padding-left: 25%;
    color: #00e3c5;
    font-size: 1.2vw;
    margin-bottom: -5%;
    font-weight: bold;
}
.game-detail-div{
    height: 70px;
    margin-top: 10px;
}
.game-image{
    height: 100%;
    width: 70px;
    float: left;
    margin-right: 5%;
}
.game-info{
    height: 100%;
    width: 40%;
    float: left;
    color: white;
    text-align: start;
    font-weight: bold;
}
.custom-fps-progress{
    margin-top : 2%
}
.show-more-data{
    margin-top: 20%;
    width: 100%;
    height: 30px;
    color: white;
    cursor: pointer;
}
.divider-line{
    margin-top: 5%;
    width: 100%;
    height: 2px;
    background: white;
}
.total-price{
    color: white;
    font-size: 1.6vw;
    margin-top: 5%;
}
.emi-option{
    color: white;
}
.center-text{
    text-align: start;
    margin-left: -15px;
}
.input-field{
    width: 100%;
}
.model-body{
    padding: 5%;
}
.submit-info-button{
    background: #4CAF50;
    height: 40px;
    width : 120px;
    font-size: 1.3vw;
    text-align: center;
    margin-left: 40%;
    border-radius: 5px;
    padding-top: 2%;
    color : white;
    font-weight: bold;
    cursor: pointer;
}
.tooltip-class{
    font-size: 1vw;
    font-weight: bold;
    opacity: 1;
}
.opaque {
    opacity: 1 !important;
}
.selected-platform{
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 1.1vw;
}
.unselected-platform{
    color: #eee;
    border: 0.5px solid #CFD8DC;
    font-weight: 100;
    cursor: pointer;
    font-size: 0.8vw;
    border-radius: 6px;
    padding: 5px;
}
.slider{
  padding-top: 15%;
}
.software-info{
    height: 100%;
    width: 40%;
    padding-top: 5%;
    float: left;
    color: white;
    text-align: start;
    font-weight: bold;
}
.ram-mem-data{
    width: 60%;
    float: left;
    text-align: left;
}
.ram-freq-data{
    width: 40%;
    float: left;
    text-align: left;
}
.configuration{
    width: 41.67%;
    height: 200%;
    float: left;
}
.item-display{
    width: 41%;
    height: 100%;
    float: left;
}
.category-icon-div{
    height : 100%;
    width : 10%;
    float : left;
    padding-top : 3%;
    font-size : 1.1vw;
}
.category-div{
    height : 100%;
    width : 30%;
    color : white;
    padding-top : 3%;
    float : left;
    font-size : 1.1vw;
    text-align : start;
}
.category-value-div{
    height : 100%;
    width : 45%;
    color : white;
    padding-top : 3%;
    float : left;
    font-size : 1.1vw;
    text-align : start;
    cursor : pointer;
}
.menu-style{
    font-size : 0.8vw;
    color : black;
}
.select-style{
    font-size : 1.1vw;
    text-align : start;
    color : white;
}
.next-button-style-final{
    margin-top : 10%;
    width: 80%;
    height : 25%;
    border-radius : 25px;
    background : #FFFFFF;
    text-align : center;
    font-size : 2vw;
    font-weight : bold;
    padding-top : 2%;
    color : #455A64;
    cursor: pointer;
}
.build-url-button{
    margin-top : 10%;
    width: 50%;
    height : 60%;
    margin-left: 20%;
    border-radius : 25px;
    background : #FFFFFF;
    text-align : center;
    font-size : 1.5vw;
    padding-top: 1%;
    font-weight : bold;
    color : #455A64;
    cursor: pointer;
}
.generate-build-url{
    cursor: pointer;
}
.generate-build-url:hover{
    text-decoration: underline;
}
.sample-space-div{
    margin-top: 18%;
}
