.game-main{
    height: 100%;
    width: 100%;
}
.section-header{
    width: 100%;
    height: 10%;
    text-align: start;
    color: white;
    text-transform: uppercase;
    padding-top: 2%;
    font-weight: bold;
    font-size: 1.4vw;
}
.game-select-area{
    width: 100%;
    height: 80%;
    max-height: 80%;
    overflow-y: scroll;
    text-align: start;
    color: white;
    text-transform: uppercase;
    padding-top: 2%;
    font-weight: bold;
    font-size: 1.4vw;
}
.footer-component{
    width: 100%;
    height: 10%;
}
.footer-start{
    height: 100%;
    width: 15%;
    float: left;
}
.footer-details{
    color: white;
    text-align: center;
    height: 100%;
    width: 70%;
    float: left;
}
.next-button{
    height: 100%;
    width: 15%;
    float: left;
}
.question-text, .feedback{
    padding-top: 2%;
    font-size: 1.3vw;
    width : 100%;
    height : 100%;
    cursor: pointer;
}
.question-text:hover{
    text-decoration: underline;
}
.data-entry{
    padding-top: 1%;
    width : 100%;
    height : 100%;
}
.data-entry-box{
    margin-left: 25%;
    width: 40%;
    height: 90%;
    color: black;
    font-size: 1.2vw;
    text-align: center;
    float: left;
}
.data-entry-box:focus{
    outline: none;
}
.send-btn{
    cursor: pointer;
    font-weight: bold;
    font-size: 2vw;
    width: 20%;
    float: left;
}
.send-btn:hover{
    font-size: 2.1vw;
}
.game-box{
    height : 40%;
    width : 12.5%;
    float: left;
}
@media screen and (max-width: 767px){
    .game-box{
        height : 40%;
        width : 50%;
        float: left;
    }
    .question-text, .feedback, .data-entry{
        display: none;
    }
}

