.footer-main{
    height: 100%;
    width: 100%;
}
.footer-header{
    width: 100%;
    height: 10%;
    text-align: center;
    padding-top : 1%;
    font-weight : bold;
    font-size: 1vw;
    color: white;
}
.footer-partner{
    width: 100%;
    height: 8%;
    margin-top: 2%;
    text-align: start;
    font-weight : bold;
    font-size: 1vw;
    color: white;
}
.footer-greet{
    width: 100%;
    height: 8%;
    margin-top: 5%;
    text-align: center;
    font-size: 1vw;
    color: white;
}
.footer-mail{
    width: 100%;
    height: 8%;
    margin-top: 2%;
    text-align: center;
    font-size: 1vw;
    color: white;
}
.footer-social{
    width: 100%;
    height: 10%;
    margin-top: 1%;
    text-align: center;
    font-size: 1vw;
    color: white;
}
.footer-copyright{
    width: 100%;
    height: 10%;
    margin-top: 1%;
    text-align: center;
    font-size: 0.7vw;
    color: white;
}
.image-size-1 {
    padding: 2%;
    max-width:70%;
    max-height:50%;
}
.image-size-2 {
    padding-top: 30%;
    max-width:100%;
    max-height:70%;
}
.image-size-3 {
    max-width:100%;
    max-height:100%;
}
.image-size-4 {
    max-width:100%;
    max-height:100%;
}
.image-size-5 {
    max-width:100%;
    max-height:100%;
}
.image-size-6 {
    padding-top: 30%;
    max-width:100%;
    max-height:100%;
}
@media screen and (max-width: 767px){
    .image-size-1 {
        max-width:13%;
        max-height:13%;
        margin-left : 15%;
        margin-right: 2%;
        float: left;
    }
    .image-size-2 {
        padding-top: 2%;
        max-width:10%;
        max-height:10%;
        margin-right: 2%;
        float: left;
    }
    .image-size-3 {
        max-width:10%;
        max-height:10%;
        margin-right: 2%;
        float: left;
    }
    .image-size-4 {
        max-width:10%;
        max-height:10%;
        margin-right: 2%;
        float: left;
    }
    .image-size-5 {
        max-width:10%;
        max-height:10%;
        margin-right: 2%;
        float: left;
    }
    .image-size-6 {
        padding-top: 2%;
        max-width:10%;
        max-height:10%;
        margin-right: 2%;
        float: left;
    }
    .footer-header{
        width: 100%;
        height: 10%;
        text-align: center;
        padding-top : 1%;
        font-weight : bold;
        font-size: 15px;
        color: white;
    }
    .footer-greet{
        width: 100%;
        height: 15%;
        margin-top: 8%;
        text-align: center;
        font-size: 15px;
        color: white;
    }
    .footer-mail{
        width: 100%;
        height: 8%;
        margin-top: 2%;
        text-align: center;
        font-size: 10px;
        color: white;
    }
    .footer-copyright{
        width: 100%;
        height: 10%;
        margin-top: 1%;
        text-align: center;
        font-size: 10px;
        color: white;
    }
}