.navbar-space{
  height: 100%;
  width: 100%;
  font-size: 1vw;
}
nav.navbar, .navbar-default {
  background-color: #000000 !important;
  /* box-shadow: 0 2px 2px 0 #FFFFFF, 0 1px 1px 0 #FFFFFF, 0 1px 1px -2px#FFFFFF; */
  padding-top: 1%;
  height: 100%;
  width: 100%;
  margin: 0%;
}
.app-logo{
  height: 100%;
  width: 30%;
  max-width: 100%;
  max-height: 100%;
}
.navbar-default {
  background-image: none;
  border: none;
  border-radius: 0;
}

.navbar-default .navbar-nav > li > a {
  color: white;
  font-size: 1.1vw;
}

.navbar-default .navbar-brand {
  color: white;
}

.navbar-default .navbar-toggle {
  border-color: black;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: white;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: transparent;
}

.navbar-default .navbar-brand:focus, .navbar-default .navbar-brand:hover {
  color: white;
  background-color: transparent;
}

.navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover {
  font-size: 1.1vw;
  font-weight: bold;
  color: white;
}

.nav-style:hover{
  font-weight: bold;
  color: #4e54c8;
}
@media screen and (max-width: 767px){
  nav.navbar, .navbar-default {
    background-color: #000000 !important;
    padding-top: 1%;
    height: 100%;
    width: 100%;
    margin: 0%;
  }
  .navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover {
    font-size: 10px;
    font-weight: bold;
    color: black;
    background: white;
  }
  Nav{
    background: white;
  }
  .navbar-space{
    height: 100%;
    width: 100%;
    font-size: 1vw;
  }
}