.software-box-main{
    height: 100%;
    width: 100%;
    text-align: center;
    cursor: pointer;
}
.software-image-div{
    height: 60%;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 2%;
    z-index: 1;
}
.software-name{
    height: 20%;
    width: 100%;
    text-align: center;
    font-size: 1vw;
    color: #00e3c5;
    font-weight: bold;
}
.software-price{
    height: 20%;
    width: 100%;
    text-align: center;
    font-size: 1vw;
    color: white;
    font-weight: bold;
}
@media screen and (max-width: 767px){
    .software-name{
        width: 100%;
        text-align: center;
        font-size: 10px;
        color: #00e3c5;
        font-weight: bold;
    }
    .software-price{
        margin-top: 10px;
        width: 100%;
        text-align: center;
        font-size: 10px;
        color: white;
        font-weight: bold;
    }
}