.accessories-main{
    width: 100%;
    height: 100%;
    background: #000000;
    padding-top: 2%;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 2%;
    max-height: 100%;
    overflow-y: scroll;
    text-align: center;
}
.acc-header{
    width: 100%;
    text-align: start;
    padding-left: 10%;
    color: white;
    font-size: 1.1vw;
}
.accessories-data{
    margin-top: 2%;
    height: 90%;
    width: 100%;
}
.acc-card-div{
    margin: 1%;
    width: 30%;
    height: 50%;
    float: left;
    cursor: pointer;
}
.pricae-container-div{
    width: 40%;
    height: 45%;
    float: left;
    margin-right: 5%;
    margin-left: 10%;
    margin-bottom: 2%;
}
.acc-item-data-div{
    padding-top: 15px   ;
    color: white;
    font-size: 1.3vw;
    width: 100%;
    height: 70%;
    max-height: 60%;
    overflow-y: scroll;
    border: 1px solid white;
}
.acc-final-price-div{
    color: white;
    width: 100%;
    height: 30%;
    border: 1px solid white;
}
.full-width{
    text-align: start;
    width: 100%;
}
.acc-shipping-detail{
    text-align: start;
    font-size: 0.8vw;
    color: white;
}
.final-price-acc{
    text-align: start;
    font-size: 1.4vw;
}
.bold-highlight{
    font-size : 1.4vw;
    color : white;
    font-weight : bold;
}
.italics-highlight{
    font-size : 1.2vw;
    color : white;
    font-weight : bold;
}
.price-div-height{
    height: 100%;
}
.acc-pricing-div{
    width : 100%;
    height : 45%;
}
.width-60{
    width: 60%;
    float: left;
}
.width-40{
    width: 40%;
    float: left;
}
.acc-next-button-style{
    width: 100%;
    height : 70%;
    border-radius : 25px;
    background : #00e3c5;
    text-align : center;
    font-size : 1.7vw;
    margin-top :5%;
    margin-bottom :2%;
    font-weight : bold;
    padding-top : 2%;
    color : white;
    cursor: pointer;
}
@media screen and (max-width: 767px){
    .acc-header{
        width: 100%;
        text-align: start;
        padding-left: 10%;
        color: white;
        font-size: 12px;
    }
    .bold-highlight{
        font-size : 12px;
        color : white;
        font-weight : bold;
    }
    .italics-highlight{
        font-size : 12px;
        color : white;
        font-weight : bold;
    }
    .acc-card-div{
        margin: 1%;
        width: 45%;
        height: 30%;
        float: left;
        cursor: pointer;
    }
    .price-div-height{
        height: 20%;
    }
    .acc-final-price-div{
        color: white;
        width: 100%;
        height: 100%;
        border: 1px solid white;
    }
    .final-price-acc{
        text-align: start;
        font-size: 10px
    }
    .acc-pricing-div{
        width : 100%;
        height : 100%;
    }
    .acc-item-data-div{
        padding: 10px;
        color: white;
        font-size: 10px;
        width: 100%;
        height: 60%;
        max-height: 60%;
        overflow-y: scroll;
        border: 1px solid white;
    }
    .acc-shipping-detail{
        text-align: start;
        font-size: 8px;
        color: white;
    }
    .acc-next-button-style{
        width: 100%;
        height : 50%;
        border-radius : 25px;
        background : #00e3c5;
        text-align : center;
        font-size : 20px;
        margin-bottom :2%;
        font-weight : bold;
        padding-top : 2%;
        color : white;
        cursor: pointer;
    }
}