.game-box-main{
    height: 100%;
    width: 100%;
    text-align: center;
    cursor: pointer;
}
.game-image-div{
    height: 80%;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    z-index: 1;
}
.game-image-div:hover{
    height: 80%;
    width: 85%;
    margin-left: 5%;
    margin-right: 5%;
    z-index: 1;
    opacity: 0.8;
}
.game-name{
    height: 20%;
    width: 100%;
    text-align: center;
    font-size: 1vw;
    color: white;
    font-weight: bold;
}
@media screen and (max-width: 767px){
    .game-name{
        height: 20%;
        width: 100%;
        text-align: center;
        font-size: 10px;
        color: white;
        font-weight: bold;
    }   
}