.App {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  pointer-events: auto;
  text-align: center;
}
Router{
  width: 100%;
  height: 100%;
}
.navbar-section{
  width: 100%;
  height: 9%;
}
.full-height{
  height: 100%;
}
.pages{
  width: 100%;
  height: 91%;
}
.bg-pages{
  width: 100%;
  height: 100%;
  background-image: url("/assets/banner.png");  /* fallback for old browsers */
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.App-link {
  color: #61dafb;
}

.social-icon{
  height: 30px;
  width: 30px;
  text-align: center;
  display: inline-block;
  border: 1px solid white;
  margin-left: 1%;
  margin-right: 1%;
  border-radius: 50%;
  padding-top: 4px;
  padding-left: 6px;
  color: white;
  cursor: pointer;
}
.full-height{
  height: 100%;
}
#overlay {
  padding-top:20%;
  display: inline-block;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.9);
  z-index: 2;
  cursor: pointer;
}
.app-footer{
  width: 100%;
  height: 50%;
  background: #000;
  padding-top: 1.5%;
  margin: 0%;
}
.container{
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 767px){
    .in-mobile-disappear{
        display: none;
    }
}