.form-factor-main{
    width: 100%;
    height: 100%;
    background: #000000;
    padding-top: 3%;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 2%;
    text-align: center;
}
.form-header{
    width: 100%;
    text-align: start;
    padding-left: 20%;
    color: white;
    font-size: 1.4vw;
    font-weight: bold;
}
.form-selection-div{
    padding-right: 20%;
    padding-left: 15%;
    margin-top: 5%;
    display: inline-block;
    height: 75%;
    width: 100%;
}
.form-data-entry{
    width: 40%;
    height: 70%;
    float: left;
    margin-left: 10%;
}
.form-image-div{
    width: 100%;
    height: 80%;
    float: left;
    margin-left: 10%;
}
.footer-form{
    width: 100%;
    height: 10%;
}
.image-size{
    max-width:100%;
    max-height:100%;
    background : #000000;
}
.image-size-form-1{
    max-width:100%;
    max-height:100%;
    background : #000000;
    margin-left: -30%;
}
.select-text{
    font-size : 1vw;
    color : #ffffff;
    margin-top : 7%;
    margin-left : 5%;
    float : left;
    font-weight : bold;
}
@media screen and (max-width: 767px){
    .image-size{
        max-width:100%;
        max-height:100%;
        background : #000000;
    }
    .image-size-form-1{
        max-width:100%;
        max-height:100%;
        background : #000000;
        margin-top:65%;
    } 
    .select-text{
        font-size : 10px;
        color : #ffffff;
        margin-top : 7%;
        margin-left : 5%;
        float : left;
        font-weight : bold;
    }
}