.transaction-main{
    height: 100%;
    width: 100%;
    text-align: center;
    background: #000;
    padding-left: 15%;
    padding-right: 15%;
    padding-top: 2%;
    padding-top: 2%;
}
.transaction-header{
    color: white;
    font-size: 1.5vw;
    text-align: start;
    border-bottom: 2px solid white;
    height: 10%;
    width: 100%;
}
.transaction-area{
    padding-top: 2%;
    height: 90%;
    width: 100%;
    max-height: 90%;
    overflow-y: scroll;
}
.transaction-card-div{
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
}
.header_1{
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
    margin-top: 10px;
}
.header_2{
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 12px;
    margin-top: 10px;
}
.input-mobile{
    width: 100%;
    margin-top: 20px;
    font-size: 20px;
    text-align: center;
}
.submit-button-transaction{
    padding-top: 2%;
    font-size: 14px;
    margin-top: 20px;
    height : 40px;
    text-align: center;
    background: black;
    color : white;
    cursor: pointer;
}
.no-transactions{
    color: white;
    font-size: 20px;
    text-align: center;
}