.MuiPaper-elevation1 {
    height: 100%;
    padding: 2%;
}
.slider-box {
    width : 100%;
    height : 100%;
    float : left;
}
.slider-div {
    height : 100%;
    width : 60%;
    float : left;
    position : fixed;
}
.data-div{
    height : 100%;
    width : 20%;
    padding-left : 5%;
    font-size : 1.5vw;
    margin-left : 85%;
    text-transform : lowercase;
    font-family: Arial, Helvetica, sans-serif;
}
.price-slider-main{
    height : 100%;
    padding-top : 2%;
    padding-bottom : 2%;
    padding-left : 2;
}
@media screen and (max-width: 767px){
    .slider-box {
        width : 100%;
        height : 100%;
    }
    .MuiSlider-valueLabel{
        height: 20px;
        width: 20px;
    }
    .data-div{
        height : 50%;
        width : 100%;
        padding-left : 5%;
        font-size : 15px;
        margin-left : 85%;
        text-transform : lowercase;
        font-family: Arial, Helvetica, sans-serif;
    }
    .slider-div {
        height : 5%;
        width : 60%;
        position : fixed;
    }
    .price-slider-main{
        height : 100%;
        padding-top : 2%;
        padding-bottom : 2%;
        padding-left : 2;
    }
}
