.price-main{
    height: 100%;
    width: 100%;
    text-align: center;
    cursor: pointer;
}
.section-header{
    width: 100%;
    height: 10%;
    text-align: start;
    color: white;
    text-transform: uppercase;
    padding-top: 2%;
    font-weight: bold;
    font-size: 1.4vw;
}
.price-select-area{
    display: inline-block;
    width: 100%;
    height: 80%;
    text-align: start;
    color: white;
    text-transform: uppercase;
    padding-top: 2%;
    font-weight: bold;
    font-size: 1.4vw;
}
.price-slider-box{
    width: 80%;
    height: 20%;
    background: white;
    border-radius: 10px;
    margin-top: 2%;
    margin-left: 10%;
    margin-right: 10%;
}
.footer-component{
    width: 100%;
    height: 10%;
}
.footer-start{
    height: 100%;
    width: 15%;
    float: left;
}
.footer-details{
    color: white;
    text-align: center;
    height: 100%;
    width: 70%;
    float: left;
}
.next-button{
    height: 100%;
    width: 15%;
    float: left;
}
.select-next-button-style{
    width: 100%;
    height : 100%;
    border-radius : 25px;
    background : #FFFFFF;
    text-align : center;
    font-size : 2.2vw;
    font-weight : bold;
    padding-top : 2%;
    color : #455A64;
    cursor: pointer;
}
@media screen and (max-width: 767px){
    .section-header{
        width: 100%;
        height: 10%;
        text-align: start;
        color: white;
        text-transform: uppercase;
        padding-top: 2%;
        font-weight: bold;
        font-size: 12px;
    }
    .select-next-button-style{
        width: 100%;
        height : 100%;
        border-radius : 15px;
        background : #FFFFFF;
        text-align : center;
        font-size : 15px;
        font-weight : bold;
        padding-top : 5%;
        margin-top: 5%;
        color : #455A64;
        cursor: pointer;
    }
    .footer-details{
        color: white;
        text-align: center;
        height: 100%;
        width: 50%;
        float: left;
    }
    .next-button{
        height: 80%;
        font-size: 20px;
        width: 35%;
        float: left;
    }
}